<template>
  <div class="main">
    <!-- 顶部导航 -->
    <div class="pc-top">
      <header-pc></header-pc>
    </div>
    <div class="phone-top">
      <header-phone></header-phone>
    </div>
    
    <div>
      <router-view></router-view>
    </div>

    <!-- 侧边栏 -->
    <div class="pc-sidebar"> 
      <sidebar-pc></sidebar-pc>
    </div>
      
  </div>
</template>

<script>
import headerPc from "@/components/header/header-pc.vue";
import headerPhone from "@/components/header/header-phone.vue";
import sidebarPc from "@/components/sidebar/sidebar-pc.vue";

export default {
  components: { headerPc, headerPhone, sidebarPc },
  created(){
    //获取菜单
    this.getMenu();

    
  },
  methods: {
    getMenu(){
      this.$http.get("/static/menu.json").then(res => {
        let data = res.data.data.result;
        this.$store.commit('getMenu', data);
      }).catch(err=>{

      });
    },

  },
};
</script>

<style lang="less" scoped>
.main{
    height: 100vh;
}

// phone
@media screen and (max-width: 820px) {
    .pc-sidebar,
    .pc-top {
      display: none;
    }
    .phone-top{
      display: block;
    }
}
//pc
@media screen and (min-width: 821px) {
    .pc-sidebar,
    .pc-top{
      display: block;
    }
    .phone-top{
      display: none;
    }
}
</style>



